import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react'
import { tsv } from 'd3-fetch'
import { scaleLinear, scaleLog } from '@visx/scale'
import { compact, groupBy, map, mapValues, range, uniq } from 'lodash'
import { Checkbox, CheckboxGroup, Circle, Flex, Heading, HStack } from "@chakra-ui/react"
import { saveAs } from 'file-saver'

import srcData from './data.tsv'
import ScatterLink from '../../components/Charts/ScatterLink'
import Sizer from '../../components/Charts/Sizer'
import Box from '../../components/Box'
import Button from '../../components/Button'
import RadioButtons from '../../components/RadioButtons'

const highlights = [
  {
    ICT產業: true,
    非ICT產業: true,
  },
  {
    顯示器: true,
    半導體: true,
    通訊: true,
    電路板: true,
    資訊及通信: true,
    光電: true,
    消費性電子: true,
    資通訊: true,
    財政部產業定義: {
      電子零組件: true,
      光學器材: true,
    },
  },
  {
    機械: true,
  },
  {
    醫療器材: true,
    美妝: true,
    傢俱: true,
    財政部產業定義: {
      礦產品: true,
      化學品: true,
      紡織品: true,
      基本金屬及其製品: true,
      機械: true,
      電機產品: true,
      運輸工具: true,
    }
  },
]

const GrowthScatter = () => {
  const svgRef = useRef()
  const [data, setData] = useState()
  const [page, setPage] = useState('0')
  const highlight = highlights[page]
  const [groups, setGroups] = useState([])
  const [activeGroup, setActiveGroup] = useState([])
    useEffect(() => {
    tsv(srcData, (d) => ({
      ...mapValues(d, v => {
        const n = Number(v)
        return isNaN(n) ? v : n
      }),
      id: d.industry + d.item,
    })).then(d => {
      const com = compact(d)
      setData(com)
      setGroups(map(groupBy(com, 'industry'), (items, indu) => [indu, items.length]))
    })
  }, [])
  const filteredData = useMemo(() => {
    if (data) {
      return data && data.filter(d => d['2020Q1'] > 0 && d['2021Q1'] > 0 && (activeGroup.length ? activeGroup.includes(d.industry) : true))
    }
    return data
  }, [data, activeGroup])
  const handleDownload = useCallback(() => {
    const date = new Date().toLocaleDateString()
    const serializer = new XMLSerializer();
    let source = serializer.serializeToString(svgRef.current);
    if(!source.match(/^<svg[^>]+xmlns="http\:\/\/www\.w3\.org\/2000\/svg"/)){
      source = source.replace(/^<svg/, '<svg xmlns="http://www.w3.org/2000/svg"');
    }
    if(!source.match(/^<svg[^>]+"http\:\/\/www\.w3\.org\/1999\/xlink"/)){
      source = source.replace(/^<svg/, '<svg xmlns:xlink="http://www.w3.org/1999/xlink"');
    }

    //convert svg source to URI data scheme.
    const blob = new Blob([source], { type: 'image/svg+xml' });
    saveAs(blob, `${date}.svg`);
  }, [])
  return (
    <Box p="2em">
      <Heading mb="0.5em">2021 Q1貿易額變化</Heading>
      <CheckboxGroup onChange={setActiveGroup}>
        <Flex flexWrap="wrap" mx="-0.25em">
          {groups.map(([g, count]) => (
            <Box key={g} m="0.25em">
              <Checkbox value={g}>{g} ({count})</Checkbox>
            </Box>
          ))}
        </Flex>
      </CheckboxGroup>
      <RadioButtons
        name="page"
        defaultValue={page}
        options={highlights.map((h, value) => ({ value: String(value), label: value + 1 }))}
        onChange={setPage}
        my="1em"
      />
      <Sizer ratio={21 / 7}>
        <ScatterLink
          data={filteredData}
          xScale={scaleLog}
          yScale={scaleLinear}
          series={[
            { x: d => d['2020Q1'], y: d => d['gr_2020Q1'], label: '2020 Q1' },
            { x: d => d['2021Q1'], y: d => d['gr_2021Q1'], label: '2021 Q1' },
          ]}
          highlight={highlight}
          ref={svgRef}
        />
      </Sizer>
      <Box my="1em">
        <Button onClick={handleDownload}>下載SVG</Button>
      </Box>
    </Box>
  )
}

export default GrowthScatter
